import React from 'react';
import Header from '../../components/Header';
import Footer from '../../components/Footer';
import logo2 from '../../assets/logo2.svg';
import { Link } from 'react-router-dom';


export default function Duvidas() {
 return (
    <div>
   <Header/>
   <div className='animation'>
    <div className='frase'>
        <h1> Leer en espanl</h1>
        <Link to='/preguntas'>
          <span></span>
          <span></span>
          <span></span>
          <span></span>
          Clique aqui
          </Link>
       </div>
       <div  className='circle'></div>
       <svg>
         <filter id="wavy">
           <feTurbulence x="0" y="0" baseFrequency="0.009" numOctaves="5" seed="2">
            <animate attributeName="baseFrequency" dur="60s" values="0.02;0.005;0.02" repeatCount="indefinite"></animate>
             </feTurbulence>
             <feDisplacementMap in="SourceGraphic" scale="30"></feDisplacementMap>
           </filter>
       </svg>
       </div>
    <div className=" doc duv">
        <div>
        <h2 class=" d-flex justify-content-start ml-3">DVIDAS FREQUENTES </h2>
        <h3 class=" d-flex justify-content-start text-align-center ml-3"> Quero me tornar solicitante de refgio</h3>
        <h5 class="ml-3 justify-content-start">1)No tenho documento algum. Posso solicitar o refgio?</h5>
            <span class="">
            Sim. A ausncia de documentos no impede a apresentao e o recebimento da solicitao reconhecimento </span><br /> 
            <span>
                da condio de refugiado. No entanto, se tornar mais complexa a anlise do pedido.
            </span>
        </div>
        <h5 class="ml-3 justify-content-start">2)Preciso agendar o atendimento na Polcia Federal?</h5>
            <span class="">
            Cada Polcia Federal possui regras prprias de atendimento. Assim, o ideal  contatar o Departamento de polcia federal</span><br /> 
            <span> mais prximo e consultar sobre a necessidade de agendamento.
            </span>
            <h5 class=" d-flex justify-content-start m-3"> 3)Tenho mais de um documento de identificao. <br/>Qual devo apresentar ao fazer a solicitao de reconhecimento da condio de refugiado?</h5>
            <span class="d-none d-lg-flex">
            Sempre que voc tiver o passaporte, utilize-o para solicitar refgio. </span> <br/> <span>  Se o passaporte no tiver os dados de filiao, ento voc pode utilizar os documentos abaixo, na seguinte ordem:
                </span> <br/>  <ul>
                    <li class="d-none d-lg-flex"> certido de nascimento;</li>
                    <li class="d-none d-lg-flex"> certido de casamento;</li>
                    <li class="d-none d-lg-flex">
                        certido consular do pas de nacionalidade; ou
                        justificao judicial.
                    </li>
                </ul>
                <span class="d-none d-lg-flex">
                Caso tenha dvidas, consulte o artigo 68 do Decreto n 9.199/1997.
                </span>
            <div class=" logo3">
               <img src={logo2}/>
            </div>
        </div>
    <div class="doc duv">
        <h3 class=" d-flex justify-content-start ml-3 ">Sou solicitante do refgio. E agora?</h3>
        <h5 class="ml-3 justify-content-start">1)O que  o Protocolo de Refgio? Qual sua validade e como renov-lo?</h5>
            <span class="">
            Como solicitante de reconhecimento da condio de refugiado, o imigrante possui o Protocolo 
            </span> <br/> <span>de Refgio, que serve como identidade e atesta sua condio migratria regular no pas, desde que dentro da validade.  </span> <br/> <span>

            Portanto,  fundamental que este Protocolo esteja dentro da validade enquanto estiver em trmite o processo de solicitao de reconhecimento da condio de refugiado.
            </span> <br/> <span> O Protocolo possui validade de um ano e dever ser renovado em qualquer unidade da Polcia Federal. </span> <br/> <span> 
            Em outras palavras: o Protocolo de Refgio dever ser renovado a cada ano enquanto o processo estiver em anlise pelo Comit Nacional para os Refugiados (Conare).
            </span> <br/> <span>
            Para renovar seu protocolo, verifique qual  a sua situao:

            </span> <br/> <span>a) se o seu Protocolo de Refgio ainda no  do Sisconare, voc precisa se registrar no Sisconare e preencher o formulrio de recadastro.
            </span> <br/> <span> Ao final, ser gerado um nmero de controle que voc dever apresentar em uma unidade da Polcia Federal (PF) para finalizar a renovao.

            </span> <br/> <span> b) se o seu Protocolo de Refgio j  do Sisconare, basta comparecer  Polcia Federal para renov-lo (verifique a necessidade de agendamento na Polcia Federal).

            </span>
        <h5>2)Como fao para obter o Documento Provisrio de Registro Nacional Migratrio (DPRNM), previsto no Decreto n 9.277/2018?</h5>
            <span>
            O Documento Provisrio de Registro Nacional Migratrio (DPRNM) ser processado pela Polcia Federal no momento em que o solicitante pedir refgio pela primeira vez </span><br />
            <span>             
       ou no momento em que for renovar o Protocolo de Refgio.
            </span>
            <br/>
            <span className="pb-3">
            Ou seja: o solicitante no precisa adotar nenhuma medida adicional: caso queira pedir refgio pela primeira vez, dever cadastrar-se no Sisconare </span> <br/> <span className="ml-3 justify-content-start d-lg-block d-none">
                e preencher o formulrio de solicitao de reconhecimento da condio de refugiado; </span> <br/>
                 <span className="d-none d-lg-flex">
                caso j seja solicitante, dever seguir as orientaes de renovao de acordo com o seu caso (realizar recadastro ou apenas agendar atendimento na Polcia Federal).
                </span>

                <h5 className="d-none d-lg-flex">3)Qual a diferena entre o Protocolo de Refgio e o Documento Provisrio de Registro Nacional Migratrio (DPRNM)?</h5>
               <span className="d-none d-lg-flex"> O Protocolo de Refgio  o documento que o solicitante recebe imediatamente ao pedir o reconhecimento da condio de refugiado </span> <br/>
                 <span className="d-none d-lg-flex">
            e ser utilizado como documento de identidade at o recebimento do Documento Provisrio de Registro Nacional Migratrio (DPRNM).
            </span>
            <br/>
                 <span className="d-none d-lg-flex">O DPRNM, previsto no Decreto n 9.277/2018, passou a ser o documento de identificao de solicitantes de refgio, em substituio do Protocolo de Refgio.</span>

                  <br/>
                 <span> LEMBRE-SE: o DPRNM possui validade de um ano, e deve ser renovado anualmente na Polcia Federal.</span>
        </div>
        <div className='doc duv pt-5 mt-5'>
            <h3>Entrevistas de Elegibilidade</h3>
            <h5>1) Posso escolher o modo de realizao da entrevista (presencial ou online)?</h5>
            <span>
            No, a deciso sobre o modo de realizao de entrevista  da Coordenao-Geral do Conare.</span> <br/> <span> 
            No entanto, essa deciso  baseada nos dados de contato do solicitante  em especial, no endereo do solicitante.</span> <br/> <span> 
            Assim,  muito importante assegurar que seus dados estejam sempre atualizados.

            </span> <br/> <span> Para atualizar seus dados de contato no Sisconare, acesse o sistema e, aps realizar o login, selecione a opo Atualizar meus dados. <br/></span>
            <span>
            Ou seja: o solicitante no precisa adotar nenhuma medida adicional: caso queira pedir refgio pela primeira vez, dever cadastrar-se no Sisconare </span> <br/> 
            <span>
                e preencher o formulrio de solicitao de reconhecimento da condio de refugiado;
                </span> 
                <h5>2) Posso escolher o local de realizao da entrevista?</h5>
                <span>
                No, a deciso sobre o local de realizao de entrevista  da Coordenao-Geral do Conare.
                </span> <br/> <span> No entanto, essa deciso  baseada nos dados de contato do solicitante  em especial, no endereo do solicitante.
                </span> <br/> <span> Assim,  muito importante assegurar que seus dados estejam sempre atualizados.

                </span> <br/> <span> Para atualizar seus dados de contato no Sisconare, acesse o sistema e, aps realizar o login, selecione a opo Atualizar meus dados.
           </span>
           <h5>3) Caso minha entrevista esteja demorando muito, como posso solicitar mais urgncia?</h5>
                <span>
                As entrevistas seguem, em regra, ordem cronolgica; no entanto, um dos critrios de priorizao  a manuteno de dados cadastrais atualizados.
                </span> <br/> <span>  Assegure-se, dessa forma, que os seus dados esto sempre atualizados.
                </span> <br/> <span>  Caso haja mudana de telefone, de e-mail ou de endereo, atualize seus dados no Sisconare, no menu "Atualizar Meus Dados".

                </span> <br/> <span> Lembre-se de que, enquanto o seu pedido de refgio est em anlise, a sua situao migratria no Brasil est regular </span> <br/> 
                <span> 
            pois voc possui autorizao provisria de residncia at a deciso final do Conare (art. 156, 3, do Decreto n 9.199/2017 e art. 21 da Lei n 9.474, de 1997).
           </span>
           </div>
        <div className='doc duv'>
        <h3 class=" d-flex justify-content-start ml-3">Mercado de trabalho</h3>
        <h5 class="ml-3 justify-content-start">1)Como obter acesso a Carteira de Trabalho Digital?</h5>
            <span >
            A Carteira de Trabalho Digital pode ser acessada pelo computador ou ser instalada em Smartphones </span> <br/> 
                <span>  com sistema operacional Android e iOS e funcionar como uma extenso do documento fsico.</span> <br/> 
                <span> 
            Veja no site do Ministrio da Economia as orientaes sobre como acessar a Carteira de Trabalho Digital.
            </span> 
            <h5 class="ml-3 justify-content-start">2) Sou empregador e quero contratar formalmente um<br className='d-block d-lg-none'/> solicitante  de reconhecimento da condio de refugiado.</h5>
            <span>
            Solicitantes de reconhecimento da condio de refugiado esto em condio migratria regular no Brasil e, dessa forma, podem ser contratados formalmente.</span> <br/> 
                <span > 
            O amparo legal de tal autorizao est na Lei n 9.474, de 22 de julho de 1997.
            </span>
            </div>

          <Footer/>
   </div>
 )}